	module.exports = {"data":{"challengeNode":{"title":"Problem 385: Ellipses inside triangles","description":["For any triangle T in the plane, it can be shown that there is a unique ellipse with largest area that is completely inside T.","","","","For a given n, consider triangles T such that:","- the vertices of T have integer coordinates with absolute value ≤ n, and ","- the foci1 of the largest-area ellipse inside T are (√13,0) and (-√13,0).","Let A(n) be the sum of the areas of all such triangles.","","","For example, if n = 8, there are two such triangles. Their vertices are (-4,-3),(-4,3),(8,0) and (4,3),(4,-3),(-8,0), and the area of each triangle is 36. Thus A(8) = 36 + 36 = 72.","","","It can be verified that A(10) = 252, A(100) = 34632 and A(1000) = 3529008.","","","Find A(1 000 000 000).","","","","1The foci (plural of focus) of an ellipse are two points A and B such that for every point P on the boundary of the ellipse, AP + PB is constant."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-385-ellipses-inside-triangles","blockName":"Project Euler","tests":[{"text":"<code>euler385()</code> should return 3776957309612154000.","testString":"assert.strictEqual(euler385(), 3776957309612154000, '<code>euler385()</code> should return 3776957309612154000.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler385() {\n  // Good luck!\n  return true;\n}\n\neuler385();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-386-maximum-length-of-an-antichain","id":"5900f4ee1000cf542c510000"},"slug":"/coding-interview-prep/project-euler/problem-385-ellipses-inside-triangles"}}

